function onBigGroupMsgNotify(msgList,callback) {
    for (var i = msgList.length - 1; i >= 0; i--) {//
        var msg = msgList[i];
        //console.warn(msg);
        webim.Log.warn('receive a new avchatroom group msg: ' + msg.getFromAccountNick());
        //
        callback(showMsg(msg));
        //showMsg(msg);
    }
}
