//newMsgList [Msg]
function onMsgNotify(newMsgList,callback) {
    var sess, newMsg;
    //
    var sessMap = webim.MsgStore.sessMap();
    for (var j in newMsgList) {//
        newMsg = newMsgList[j];

        if (newMsg.getSession().id() == selToID) {//
            selSess = newMsg.getSession();
            //
            //console.log(newMsg);
            handlderMsg(newMsg,callback);//
            //
            webim.setAutoRead(selSess, true, true);
        }

    }


    for (var i in sessMap) {
        sess = sessMap[i];
        if (selToID != sess.id()) {//
            //updateSessDiv(sess.type(), sess.id(), sess.unread());
        }
    }
}
//()()
function handlderMsg(msg,callback) {
    var fromAccount, fromAccountNick, sessType, subType, contentHtml;

    fromAccount = msg.getFromAccount();
    if (!fromAccount) {
        fromAccount = '';
    }
    fromAccountNick = msg.getFromAccountNick();
    if (!fromAccountNick) {
        fromAccountNick = fromAccount;
    }

    //
    //
    //webim.SESSION_TYPE.GROUP-
    //webim.SESSION_TYPE.C2C-
    sessType = msg.getSession().type();
    //
    //webim.GROUP_MSG_SUB_TYPE
    //webim.C2C_MSG_SUB_TYPE
    subType = msg.getSubType();

    switch (sessType) {
        case webim.SESSION_TYPE.C2C://
            switch (subType) {
                case webim.C2C_MSG_SUB_TYPE.COMMON://c2c
                    //fromAccountappc2c
                    //(webim.MSG_ELEMENT_TYPE.CUSTOM,TIMCustomElem)
                    contentHtml = convertMsgtoHtml(msg);
                    // console.log(msg);
                    webim.Log.warn('receive a new c2c msg: fromAccountNick=' + fromAccountNick + ", content=" + contentHtml);
                    //c2c
                    var opts = {
                        'To_Account': fromAccount,//
                        'LastedMsgTime': msg.getTime()//
                    };
                    webim.c2CMsgReaded(opts);
                    callback(
                       {
                         fromAccountNick: fromAccountNick,
                         content: contentHtml,
                         subType: msg.subType,
                         isSend: msg.isSend,
                         time: msg.time //webim.Tool.formatTimeStamp(msg.time)
                       }
                    );
                    //console.error('c2c(): =' + fromAccountNick + ", =" + contentHtml);
                    break;
            }
            break;
        case webim.SESSION_TYPE.GROUP://
            break;
    }
}
