function getLastC2CHistoryMsgs(opt,callback,cbError) {
    if (selType == webim.SESSION_TYPE.GROUP) {
        alert('');
        return;
    }
    if(!opt.order){
      getPrePageC2CHistroyMsgInfoMap=[]
    }
    var lastMsgTime = getPrePageC2CHistroyMsgInfoMap[selToID] ? getPrePageC2CHistroyMsgInfoMap[selToID].LastMsgTime : 0;//0
    var msgKey = getPrePageC2CHistroyMsgInfoMap[selToID] ? getPrePageC2CHistroyMsgInfoMap[selToID].MsgKey : '';
    var options = {
        'Peer_Account': opt.count==1 ? opt.account: selToID, //
        'MaxCnt': opt.count, //
        'LastMsgTime': lastMsgTime, //
        'MsgKey': msgKey
    };
    webim.getC2CHistoryMsgs(
        options,
        function (resp) {
            var complete = resp.Complete;//1-0-
            var retMsgCount = resp.MsgCount;//

            if (resp.MsgList.length == 0) {
                webim.Log.error(":data=" + JSON.stringify(options));
                return;
            }
            getPrePageC2CHistroyMsgInfoMap[selToID] = { //Key,
                'LastMsgTime': resp.LastMsgTime,
                'MsgKey': resp.MsgKey
            }
            if (callback)
               callback(resp.MsgList);
        },
        cbError
    );
}
//
function formatTimeStamp(time){
  if(time==null) return
  var spanTime = '';
  const s=Math.floor(Math.round(new Date().getTime() / 1000)-time)
  if(s<60){
    spanTime = '';
  }else if(s>60 && s<3600){
    spanTime=Math.floor(s/60)+''
  }else if(s/3600<24){
    //spanTime=Math.floor(s/3600)+''
    spanTime=webim.Tool.formatTimeStamp(time).substring(11,16)
  }else if(s/3600/24==1){
    spanTime=''
  }else if(s/3600/24>1){
    spanTime=webim.Tool.formatTimeStamp(time).substring(0,10)
  }
  return spanTime;
}
