//+++
function showMsg(msg) {
    var isSelfSend, fromAccount, fromAccountNick, sessType, subType;
    var ul, li, paneDiv, textDiv, nickNameSpan, contentSpan;

    fromAccount = msg.getFromAccount();
    if (!fromAccount) {
        fromAccount = '';
    }
    fromAccountNick = msg.getFromAccountNick();
    if (!fromAccountNick) {
        fromAccountNick = '';
    }
    //
    //
    //webim.SESSION_TYPE.GROUP-
    //webim.SESSION_TYPE.C2C-
    sessType = msg.getSession().type();
    //
    //webim.GROUP_MSG_SUB_TYPE
    //webim.C2C_MSG_SUB_TYPE
    subType = msg.getSubType();

    isSelfSend = msg.getIsSend();//
    var content = "";
    switch (subType) {

        case webim.GROUP_MSG_SUB_TYPE.COMMON://
            content = convertMsgtoHtml(msg);
            break;
        case webim.GROUP_MSG_SUB_TYPE.REDPACKET://
            content = "[]" + convertMsgtoHtml(msg);
            break;
        case webim.GROUP_MSG_SUB_TYPE.LOVEMSG://
            //
            content = "[]" + convertMsgtoHtml(msg);
            //
            showLoveMsgAnimation();
            break;
        case webim.GROUP_MSG_SUB_TYPE.TIP://
            content = "[]" + convertMsgtoHtml(msg);
            break;
    }

    return {
        fromAccountNick: fromAccountNick,
        content: content,
        subType: subType,
        isSend: isSelfSend
    }
}

//Html
function convertMsgtoHtml(msg) {
    var html = "", elems, elem, type, content;
    elems = msg.getElems();//
    for (var i in elems) {
        elem = elems[i];
        type = elem.getType();//
        content = elem.getContent();//
        switch (type) {
            case webim.MSG_ELEMENT_TYPE.TEXT:
                html += convertTextMsgToHtml(content);
                break;
            case webim.MSG_ELEMENT_TYPE.FACE:
                html += convertFaceMsgToHtml(content);
                break;
            case webim.MSG_ELEMENT_TYPE.IMAGE:
                html += convertImageMsgToHtml(content);
                break;
            case webim.MSG_ELEMENT_TYPE.SOUND:
                html += convertSoundMsgToHtml(content);
                break;
            case webim.MSG_ELEMENT_TYPE.FILE:
                html += convertFileMsgToHtml(content);
                break;
            case webim.MSG_ELEMENT_TYPE.LOCATION://
                msg.subType=3;
                html += convertLocationMsgToHtml(content);
                break;
            case webim.MSG_ELEMENT_TYPE.CUSTOM:
                msg.subType=4;
                html += convertCustomMsgToHtml(content);
                break;
            case webim.MSG_ELEMENT_TYPE.GROUP_TIP:
                html += convertGroupTipMsgToHtml(content);
                break;
            default:
                webim.Log.error(': elemType=' + type);
                break;
        }
    }
    return webim.Tool.formatHtml2Text(html);
}

//
function convertTextMsgToHtml(content) {
    return content.getText();
}
//
function convertFaceMsgToHtml(content) {
    var data = content.getData();
    var index = content.getIndex();
    var faceUrl='';
    var emotion = webim.Emotions[index];
    if (emotion) {
        faceUrl = emotion.pic;
    }
    if (faceUrl) {
        return "<img src='"+faceUrl+"' width='65px'/>";
    } else {
        return data;
    }
}
//
function convertImageMsgToHtml(content) {
    var smallImage = content.getImage(webim.IMAGE_TYPE.SMALL);//
    var bigImage = content.getImage(webim.IMAGE_TYPE.LARGE);//
    var oriImage = content.getImage(webim.IMAGE_TYPE.ORIGIN);//
    if (!bigImage) {
        bigImage = smallImage;
    }
    if (!oriImage) {
        oriImage = smallImage;
    }
    return "<img src='" + smallImage.getUrl() + "#" + bigImage.getUrl() + "#" + oriImage.getUrl() + "' style='CURSOR: hand' id='" + content.getImageId() + "' bigImgUrl='" + bigImage.getUrl() + "' onclick='imageClick(this)' />";
}
//
function convertSoundMsgToHtml(content) {
    var second = content.getSecond();//
    var downUrl = content.getDownUrl();
    if (webim.BROWSER_INFO.type == 'ie' && parseInt(webim.BROWSER_INFO.ver) <= 8) {
        return '[]demoie8(),URL:' + downUrl;
    }
    return '<audio src="' + downUrl + '" controls="controls" onplay="onChangePlayAudio(this)" preload="none"></audio>';
}
//
function convertFileMsgToHtml(content) {
    var fileSize = Math.round(content.getSize() / 1024);
    return '<a href="' + content.getDownUrl() + '" title="" ><i class="glyphicon glyphicon-file">&nbsp;' + content.getName() + '(' + fileSize + 'KB)</i></a>';

}
//
function convertLocationMsgToHtml(content) {
    return JSON.stringify({
      longitude:content.getLongitude(),
      latitude:content.getLatitude(),
      store_address:content.getDesc()
    })
}
//
function convertCustomMsgToHtml(content) {
    var data = content.getData();
    var desc = content.getDesc();
    var ext = 1;//content.getExt();
    //return "data:" + data + ", desc:"+ desc +", ext:" + ext;
    return JSON.stringify({
      data:data.replace(/\s+/g, "").replace(/<[^>]+>/g,""),
      desc:desc,
      ext:ext
    })
}
