import { authApi } from '@helper/reduxFetch'
export const COLLECTSTORE_COLLECT_LIST_INIT = 'COLLECTSTORE_COLLECT_LIST_INIT' // 
export const COLLECTSTORE_COLLECT_LIST = 'COLLECTSTORE_COLLECT_LIST' // type: 
export const COLLECTSTORE_DEL_COLLECT = 'COLLECTSTORE_DEL_COLLECT' // type: 
export const COLLECTSTORE_ADD_STOTRE = 'COLLECTSTORE_ADD_STOTRE' // 
export const COLLECTSTORE_COLLECT_LODING = 'COLLECTSTORE_COLLECT_LODING' // 
export const COLLECTSTORE_COLLECT_ISLOADING = 'COLLECTSTORE_COLLECT_ISLOADING' // isLoading

