import { authApi } from '@helper/reduxFetch'
export const COLLECTPOST_COLLECT_POST_INIT = 'COLLECTPOST_COLLECT_POST_INIT' // 
export const COLLECTPOST_COLLECT_POST = 'COLLECTPOST_COLLECT_POST' // type: 
export const COLLECTPOST_DEL_POST = 'CCOLLECTPOST_DEL_POST' // 
export const COLLECTPOST_COLLECT_POST_LODING = 'COLLECTPOST_COLLECT_POST_LODING' // 
export const COLLECTPOST_ADD_POST = 'COLLECTPOST_ADD_POST' // 
export const COLLECTPOST_COLLECT_POST_ISLODING = 'COLLECTPOST_COLLECT_POST_ISLODING' // isLoading

export const collectPostinit = authApi({
  url: '/api/user/collect_list',
  action: (args, json) => {
    return {
      type: COLLECTPOST_COLLECT_POST_INIT,
      data: json.data,
    }
  },
})

export const collectPost = authApi({
  url: '/api/user/collect_list',
  action: (args, json) => {
    return {
      type: COLLECTPOST_COLLECT_POST,
      data: json.data,
    }
  },
  prelude: () => {
    return {
      type: COLLECTPOST_COLLECT_POST_LODING,
    }
  },
})

export const addPost = authApi({
  url: '/api/user/collect_list',
  action: (args, json) => {
    return {
      type: COLLECTPOST_ADD_POST,
      args,
      data: json.data,
    }
  },
  prelude: () => {
    return {
      type: COLLECTPOST_COLLECT_POST_ISLODING,
    }
  },
})

export const delCollectPost = authApi({
  url: '/api/user/delete_collect',
  action: (args, json) => {
    return {
      type: COLLECTPOST_DEL_POST,
      args,
      date: json.date,
    }
  },
})



