import React from 'react'
import PropTypes from 'prop-types'
import { userLogout } from '@helper/auth'
import { List, WhiteSpace, WingBlank, Button, Modal, NavBar } from 'antd-mobile'

class Seeting extends React.Component {
  static propTypes = {
    history: PropTypes.object,
    location: PropTypes.object,
  }
  static contextTypes = {
    router: PropTypes.shape({
      history: PropTypes.object.isRequired,
    }),
  }

  loginOut = () => {
    userLogout()
      .then(json => {
        // window.location.reload()
        this.context.router.history.replace('/login', {
          location: this.props.location,
        })
      })
  }
  render() {
    const { push } = this.props.history
    return (
      <div>
        <NavBar
          mode="dark"
          onLeftClick={() => this.props.history.go(-1)}>
          
        </NavBar>
        <List>
          <List.Item
            arrow="empty"
            onClick={() => push('/user/seeting/changemobile')}>
            
          </List.Item>
        </List>
        <WhiteSpace size="lg" />
        <List>
          <List.Item
            arrow="empty"
            onClick={() => push('/user/seeting/aboutous')}>
            
          </List.Item>
        </List>
        <WhiteSpace size="lg" />
        <WingBlank size="md">
          <Button type="primary" onClick={() => {
            Modal.alert(
              '', '',
              [
              { text: '', onPress: () => console.log('') },
              { text: '', onPress: () => { this.loginOut() } },
              ])
          }
          } ></Button>
        </WingBlank>
      </div>
    )
  }
}

export default Seeting



